.Home {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 100px 16px 100px 16px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: -webkit-gradient(linear, left top, right top, from(#fd8355), color-stop(36%, #f0576c), to(#f79cbd));
    background: -webkit-linear-gradient(left, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
    background: -o-linear-gradient(left, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
    background: linear-gradient(90deg, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
}

.Home h1, h2 {
    text-align: center;
}

.Sorting {
    margin-bottom: 10px;
    padding: 5px 7px 5px 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    -ms-flex-align: center;
        align-items: center;
}

.Sorting button {
    width: 27px;
    height: 27px;
    border: 1px solid #ffffff;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.Sorting i {
    color: #ffffff;
    font-size: 12px;
}

.Sorting button[aria-pressed="true"] i:first-of-type {
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
}

.Sorting span {
    margin-right: 0.8em;
    font-size: 16px;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.Sorting button:hover + span {
    opacity: 0.8;
}

@media (max-width: 768px) {
    .Home {
        padding-top: 50px;
    }

    .Sorting span {
        visibility: hidden;
    }

}



