.Backdrop {
    z-index: 50;
    background: rgba(0, 0, 0, 0.7);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transition: opacity 0.22s;
    -o-transition: opacity 0.22s;
    transition: opacity 0.22s;
}

.BackdropEnter {
    opacity: 0;
}

.BackdropEnterActive {
    opacity: 1;
}

.BackdropExit {
    opacity: 0;
}




